*{
    box-sizing: border-box;
}
body{
    /* background: red; */
    margin: 0;
    font-family: "karla", sans-serif;
}
.header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 65px;
    background:  linear-gradient(90deg, #672280 1.18%, #A626D3 100%);
    padding: 20px;
    color: white;
}
header .first{
    /* width: 70%; */
    height: 100%;
    /* background: red; */
    display: flex;
    align-items: center;
    gap: 0.1rem;
}
header .first .header--image{
    height: 100%;
    /* margin-right: 6px; */
}
header .first .header--title{
    font-size: 20px;
    color: white;
    /* margin-right: 100px; */

}

.header--project{
    /* margin-left: 400px; */
    font-size: 12px;
    font-weight: 500;
}
main{
    padding: 36px;
}
.form{
    display: grid;
    grid-template: 40px 40px / 1fr 1fr;
    gap: 17px;
}
.form--input{
    font-family: "karla", sans-serif;
    border-radius: 5px;
    border: 1px solid #D5D4D8;
    text-indent: 5px;
}
.form--button{
    grid-column: 1 / -1;
    font-family: "karla", sans-serif;
    border-radius: 5px;
    background:  linear-gradient(90deg, #672280 1.18%, #A626D3 100%);
    color: white;
    border: none;
    cursor: pointer;
}

.meme {
    position: relative;
}

.meme--image {
    max-width: 100%;
    border-radius: 3px;
}

.meme--text {
    position: absolute;
    width: 80%;
    text-align: center;
    left: 50%;
    transform: translateX(-50%);
    margin: 15px 0;
    padding: 0 5px;
    font-family: impact, sans-serif;
    font-size: 2em;
    text-transform: uppercase;
    color: white;
    letter-spacing: 1px;
    text-shadow:
        2px 2px 0 #000,
        -2px -2px 0 #000,
        2px -2px 0 #000,
        -2px 2px 0 #000,
        0 2px 0 #000,
        2px 0 0 #000,
        0 -2px 0 #000,
        -2px 0 0 #000,
        2px 2px 5px #000;
}

.bottom {
    bottom: 0;
}

.top {
    top: 0;
}